// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import gsap, {TweenMax, Power2} from "gsap";
gsap.config({ nullTargetWarn: false });



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $cursor       = $('.cursor');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($cursor.length){
   
  	$(window).on('mousemove', function(e){
    	TweenMax.to($cursor, 1, {left:  e.clientX, top:e.clientY, ease:Power2.easeOut})
  	});

  	$('[data-cursor="scale"]').on("mouseenter", function() {
		$cursor.addClass("m-scale");
	});

  	$('[data-cursor="scale"]').on("mouseleave", function() {
		$cursor.removeClass("m-scale");
	});

  	$('[data-cursor="scale-dark"]').on("mouseenter", function() {
		$cursor.addClass("m-scale").addClass("m-dark");
	});

  	$('[data-cursor="scale-dark"]').on("mouseleave", function() {
		$cursor.removeClass("m-scale").removeClass("m-dark");
	});

  	$('[data-cursor="video"]').on("mouseenter", function() {
		$cursor.addClass("m-video");
    	gsap.fromTo($cursor.find('.ico svg'), .5, { y:40},{ y:0, ease:Power2.easeOut}, 0);
	});

  	$('[data-cursor="video"]').on("mouseleave", function() {
		$cursor.removeClass("m-video");
    	gsap.fromTo($cursor.find('.ico svg'), .5, {y:0},{y:-40, ease:Power2.easeOut},0);
	});

  	$('[data-cursor="left"]').on("mouseenter", function() {
		$cursor.addClass("m-left");
    	gsap.fromTo($cursor.find('.ico svg'), .5, { y:-40},{ y:0, ease:Power2.easeOut},0);
	});

  	$('[data-cursor="left"]').on("mouseleave", function() {
		$cursor.removeClass("m-left");
    	gsap.fromTo($cursor.find('.ico svg'), .5, {y:0},{y:40, ease:Power2.easeOut},0);
	});

  	$('[data-cursor="right"]').on("mouseenter", function() {
		$cursor.addClass("m-right");
    	gsap.fromTo($cursor.find('.ico svg'), .5, { y:40},{ y:0, ease:Power2.easeOut},0);
	});

  	$('[data-cursor="right"]').on("mouseleave", function() {
		$cursor.removeClass("m-right");
	    gsap.fromTo($cursor.find('.ico svg'), .5, {y:0},{y:-40, ease:Power2.easeOut},0);
	});

  	$('[data-cursor="more"]').on("mouseenter", function() {
		$cursor.addClass("m-text");
    	gsap.fromTo($cursor.find('.text span'), .5, {opacity: 0, y:20},{opacity: 1, y:0, ease:Power2.easeOut},0);
	});

  	$('[data-cursor="more"]').on("mouseleave", function() {
		$cursor.removeClass("m-text");
    	gsap.fromTo($cursor.find('.text span'), .2, {opacity: 1,y:0},{opacity: 0,y:-40, ease:Power2.easeOut},0);
	});

}

