// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $header       	= $('.header');
var $fixed 			= $header.clone().addClass('fixed').insertAfter($header);
var $menu       	= $('.menu');
var $hmbrg      	= $('.header').find('.hmbrg');
var $btSearch      	= $('.header').find('.bt-search');
var $btSClose     	= $('.header').find('.s-close');

var _prevScrollTop 	= 0;
var _breakPoint 	= 200;



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($header.length){

	$('.header').find('.hmbrg').on('click', function(){
		if($(this).hasClass('active')){
			hideMenu();
			$('.header').find('.hmbrg').removeClass('active');
		}else{
			showMenu();
			$('.header').find('.hmbrg').addClass('active');
		}
	});

	$btSearch.on('click',function(){
		$(this).closest('.header').find('.h-search').fadeIn(200);
		$menu.addClass('open-search');
	});

	$btSClose.on('click',function(){
		$(this).closest('.header').find('.h-search').fadeOut(200);
		$menu.removeClass('open-search');
	});

	$(window).on('scroll', onScroll);
	onScroll();
}



// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ------------ \\\
// ----------------------------------------- \\\



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function showMenu(){
	$('.header').addClass('show-menu');
	$('.cursor').addClass('show-menu');
	$menu.addClass('show-menu');

	setTimeout(function(){
		$menu.addClass('motion-menu');
	}, 10);
}

function hideMenu(){
	$menu.removeClass('motion-menu');
	$('.cursor').removeClass('show-menu');

	setTimeout(function(){
		$('.header').removeClass('show-menu');
		$menu.removeClass('show-menu');
	}, 500);

	
}

function onScroll(){

	

	var dHeight 		= $(document).height();
	var wHeight			= $(window).height();
	var scrollTop 		= $(window).scrollTop();
	var isScrollingUp 	= _prevScrollTop - scrollTop > 0 ? true : false;

	if(!$('.header').hasClass('show-menu')){
		if (scrollTop < _breakPoint) {
			$fixed.removeClass('visible');
		}
		else if (scrollTop + wHeight >= dHeight) {
			// $fixed.addClass('visible');
		}
		else {
			if (isScrollingUp) {
				$fixed.addClass('visible').removeClass('force-hide');
			} else {
				$fixed.removeClass('visible').addClass('force-hide');
			}
		}
	}
	
	_prevScrollTop = scrollTop;
}
