// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import gsap, {TimelineLite, Power2} from "gsap";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $aboutIntro     = $('.about-intro');


// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
function init(){
    
    if($(window).width() > 850){
        addTransition();
    }
    
}



// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ------------ \\\
// ----------------------------------------- \\\
function resize() { 

}



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function addTransition(){


    var aboutWebdoor = new TimelineLite();
    
        aboutWebdoor.fromTo($aboutIntro.find('.title'), 4, {opacity: 0, y: '20vh' }, {opacity: 1, y: 0, ease: Power2.easeInOut}, 0)
                    .fromTo($aboutIntro.find('.abti-image').eq(0), 4, {y: '100vh'}, {y: '-50vh', ease: Power2.easeInOut}, "-=3")
                    .fromTo($aboutIntro.find('.abti-image').eq(1), 3.5, {y: '100vh'}, {y: '-50vh', ease: Power2.easeInOut}, "-=4")
                    .fromTo($aboutIntro.find('.abti-image').eq(2), 4, {y: '100vh'}, {y: '-100vh', ease: Power2.easeInOut}, "-=3.5")
                    .fromTo($aboutIntro.find('.abti-image').eq(3), 5, {y: '100vh'}, {y: '-100vh', ease: Power2.easeInOut}, "-=4")
                    .fromTo($aboutIntro.find('.caption'), 5, {opacity: 0, y: '20vh' }, {opacity: 1, y: 0, ease: Power2.easeInOut}, '-=3')
                    .fromTo($aboutIntro.find('.paragraph'), 5, {opacity: 0, y: '20vh' }, {opacity: 1, y: 0, ease: Power2.easeInOut}, '-=4')
                    .fromTo($aboutIntro.find('.about-images').find('.abt-image').eq(0), 4, {y: '100vh'}, {y: 0, ease: Power2.easeInOut},'-=4')
                    .fromTo($aboutIntro.find('.about-images').find('.abt-image').eq(1), 4.5, {y: '100vh'}, {y: 0, ease: Power2.easeInOut},'-=4')
                    .fromTo($aboutIntro.find('.about-images').find('.abt-image').eq(2), 5, {y: '100vh'}, {y: 0, ease: Power2.easeInOut, onComplete:function(){
                        $aboutIntro.fadeOut(200);
                    }},'-=6');
    

    aboutWebdoor.play();


}


// ----------------------------------------- \\\
// ---------------- EXPORTS ---------------- \\\
// ----------------------------------------- \\\
export { init, resize }




