// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $chart      = $('.chart');
var $numbers    = $chart.find('.numbers');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($chart.length){
	$(window).on('scroll', onScroll);
  onScroll();
}



// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ------------ \\\
// ----------------------------------------- \\\



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function onScroll(){
  $numbers.each(function(){
      if (isInViewport($(this))) {
        if(!$(this).hasClass('active')){
            motionNumber($(this));
        }   
      }
  });
}

function motionNumber(_this){
  var $this   = $(_this);
  var countTo = $this.attr('data-count');
  $this.addClass('active');


  if(countTo % 1 == 0){
      $({ countNum: $this.text()}).animate({countNum: countTo},{
          duration: 3000,
          easing:'linear',
          step: function() {
              $this.find('.number').text('+'+Math.floor(this.countNum));
          },
          complete: function() {
              $this.find('.number').text('+'+this.countNum);
          }
      });  
  }else{
      $({ numberValue: $this.text()}).animate({numberValue: countTo},{
          duration: 3000,
          easing:'linear',
          step: function() {
              $this.find('.number').text('+'+Math.ceil(this.numberValue*100)/100);
          },
          complete: function() {
              $this.find('.number').text('+'+this.numberValue);
          }
      });
  }
  
}

function isInViewport(_this){
  var elementTop      = $(_this).offset().top;
  var elementBottom   = elementTop + $(_this).outerHeight();
  var viewportTop     = $(window).scrollTop();
  var viewportBottom  = viewportTop + $(window).height();

  return elementBottom > viewportTop && elementTop < viewportBottom;
}
