// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import 'slick-carousel';



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $gallery		= $('.home-gallery');
var $slides       	= $gallery.find('.slides');
var $info 			= $gallery.find('.info');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($gallery.length){

    $slides.slick({
		arrows		: false,
		dots		: true,
		infinite	: true,
		padding		: '60px',
	});

	$slides.on( 'afterChange', function( e, slick, current ){
		setInfo(current);
	});

	$slides.on('beforeChange', function(event, slick, currentSlide, nextSlide){
		motionTextOut();
	});

	$gallery.find('.a-left').on('click',function(){
		$slides.slick('slickPrev');
	});

	$gallery.find('.a-right').on('click',function(){
		$slides.slick('slickNext');
	});

	$slides.slick('slickGoTo', 0);
}



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function setInfo(_index){
	var _type 	= $gallery.find('.slide').eq(_index+1).data('type');
	var _title 	= $gallery.find('.slide').eq(_index+1).data('title');
	var _link 	= $gallery.find('.slide').eq(_index+1).data('link');
	var _button = $gallery.find('.slide').eq(_index+1).data('button');

	$info.find('.g-title div').remove();
	$info.find('.g-desc-text').html(_type);
	$info.find('.button').attr('href',_link);
	$info.find('.button').find('span').html(_button);

	var _words = _title.split(' ');
	for(var i = 0; i < _words.length; i++){
		$info.find('.g-title').append('<div class="words"><span>'+_words[i]+'</span></div>');
	}

	setTimeout(function(){
		motionTextIn();
	},500);
	
}

function motionTextOut(){
	$info.find('.g-title').removeClass('motion-in');
	$info.find('.g-desc-text').removeClass('motion-in');
	$info.find('.button').removeClass('motion-in');

	$info.find('.words').each(function(i,e){
        setTimeout(function(){
            $(e).removeClass('motion-in');
        },i*20);
    });
}

function motionTextIn(){
	$info.find('.g-title').addClass('motion-in');
	$info.find('.g-desc-text').addClass('motion-in');
	$info.find('.button').addClass('motion-in');

	$info.find('.g-title').find('.words').each(function(i,e){
        setTimeout(function(){
            $(e).addClass('motion-in');
        },i*50);
    });
}
  
