// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $share          = $('.share');
var $type           = $share.find('.type');
var $items          = $share.find('.items');
var $pcGray         = ($('.pc-gray').length) ? $('.pc-gray') : $('.footer');

var _scrollTop      = null
var _offTop         = null;
var _offBtm         = null;
var _sizeScroll     = null;

// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($share.length){
  if($(window).width() < 850){
    $type.on('click',function(){
      if($(this).hasClass('active')){
        $(this).removeClass('active');
        $items.fadeOut();
      }else{
        $items.fadeIn();
        $(this).addClass('active');
      }
    });
  }else{

    _offTop             = $share.offset().top;
    _offBtm             = ($pcGray.offset().top + 40) - $share.outerHeight();

    $( window ).on('resize', function() {
        _offTop         = $share.offset().top;
        _offBtm         = ($pcGray.offset().top + 40) - $share.outerHeight();
        _sizeScroll     = $(document).height() - $(window).height() - $pcGray.outerHeight() - $share.offset().top;
    });

    $(window).on('scroll',function(e){

        _scrollTop = $(window).scrollTop();

        if (_scrollTop > _offTop && $share.hasClass('natural')) {
            $share.removeClass('natural').addClass('fixed');
        }

        if (_offTop > _scrollTop && $share.hasClass('fixed')) {
            $share.removeClass('fixed').addClass('natural');
        }

        if (_scrollTop > _offBtm && $share.hasClass('fixed')) {
          $share.removeClass('fixed').addClass('bottom').css('top', _offBtm - (_offTop));
        }
        if (_offBtm > _scrollTop && $share.hasClass('bottom')) {
            $share.removeClass('bottom').addClass('fixed').css('top', '0px');
        }
    });
  }
}



