// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $map            = $('.map');
var $iframe         = $map.find('.iframe');

var map             = null;


// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
function init(_latitude,_long,_address){

	window.initGmaps = function(){
		buildMap(_latitude, _long, _address);
	}

	if ($map.is(':visible')) {
		if (window.google && window.google.maps) initGmaps();
		else loadScript('//maps.googleapis.com/maps/api/js?key='+(getScript('gkey') || 'AIzaSyCZDAmjlUz5FY4bNaQlagvRZg2ysi92VA4')+'&callback=initGmaps');
	}
	
}


// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ----------- \\\
// ----------------------------------------- \\\


// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function buildMap(_latitude, _long, _address){

	var lat     = _latitude;
  	var lng     = _long;

  	var coords  = new google.maps.LatLng(lat, lng);
    
	var mapOptions = {
		scaleControl: false,
		draggable: true,
		scrollwheel: false,
		disableDefaultUI: true,
		panControl: true,
		mapTypeId: google.maps.MapTypeId.ROADMAP,
		zoom: 14,
		center: new google.maps.LatLng(lat,lng),
	};

	var contentString = 
	'<div class="container">'+
		'<div class="infos">'+
			'<div class="sub-title">'+_address+'</div>'+
		'</div>'+
	'</div>';

	var infowindow = new google.maps.InfoWindow({
		content: contentString
	});

	map = new google.maps.Map($iframe[0], mapOptions);

	var marker = new google.maps.Marker({
    position: coords,
    map: map,
      icon: {
          url: "./img/ico-pin.svg",
          scaledSize: new google.maps.Size(50, 50),
      }

    });

    infowindow.open(map, marker);
}

function loadScript(src, callback){
	var s = document.createElement('script'), r = false, t;
	s.type = 'text/javascript';
	s.src = src;
	s.onload = s.onreadystatechange = function(){
		if (!r && (!this.readyState || this.readyState == 'complete')) {
			r = true;
			if (typeof callback == 'function') callback();
		}
	}
	t = document.getElementsByTagName('script')[0];
	t.parentNode.insertBefore(s, t);
}


function getScript(paramKey){

	var pairs = location.search.replace(/^\?/, '').split('&');
	var params = [];

	pairs.forEach(function(item){
		var split = item.split('=');
		var key = split[0];
		var val = split[1];
		params[key] = val;
	});

	var selected = params[paramKey];
	if (selected) selected = decodeURIComponent(selected.replace(/\+/gi, ' '));

	return selected;

}



// ----------------------------------------- \\\
// ---------------- EXPORTS ---------------- \\\
// ----------------------------------------- \\\
export { init, buildMap }