import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $searchModal    = $('.search-modal');
var $btClose        = $searchModal.find('.sm-close');
var $btBackground   = $searchModal.find('.sm-background');




// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($searchModal.length){

	$btClose.on('click',function(){
        $searchModal.fadeOut(200);
        $('.filter').find('.i-search').removeClass('active');
        $('.header').removeClass('open-search');
        $('.pages').removeClass('open-search');
    });

    $btBackground.on('click',function(){
        $searchModal.fadeOut(200);
        $('.filter').find('.i-search').removeClass('active');
        $('.header').removeClass('open-search');
        $('.pages').removeClass('open-search');
    });
}



