// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $menu       	= $('.menu');
var $group			= null


// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($menu.length){
	if($(window).width() <= 850){
		var $group = $menu.find('.group');
	}else{
		var $group = $menu.find('.m-left').find('.group');
	}

	$group.on('click',function(){

		if($(this).hasClass('active')){
			$(this).find('.items').slideUp();
			$(this).removeClass('active');
		}else{

			$group.find('.items').slideUp();
			$group.removeClass('active');
			
			$(this).find('.items').slideDown().css('display','flex');
			$(this).addClass('active');
		}
	});

}



