// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $footer       	= $('.footer');
var $scrollup   	= $footer.find('.b-backUp');
var $group			= $footer.find('.group');

// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($footer.length){
	$scrollup.on('click', function(){
		$("html, body").animate({ scrollTop: 0 }, "slow");	
	});

	if($(window).width() <= 850){
		$group.on('click',function(){
			if($(this).hasClass('active')){
				$(this).find('.items').slideUp();
				$(this).removeClass('active');
			}else{
				$(this).find('.items').slideDown();
				$(this).addClass('active');
			}
		});
	}

}



