import $ from 'jquery';

// import * as PageLoad from './modules/page-load';
import './modules/scrollmagic'
import './modules/svg';

import * as Home            from './pages/home';
import * as About           from './pages/about';
import * as GetInTouch      from './pages/get-in-touch';



import  './components/cursor';
import  './components/footer';
import  './components/header';
import  './components/menu';
import  "./components/video";
import  "./components/chart";
import  "./components/home-gallery";
import  "./components/resources-gallery";
import  "./components/teams";
import  "./components/share";
import  "./components/menu-navigate";
import  "./components/filter";
import  "./components/modal-search";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
const _pages = $('main').data('page');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
// PageLoad.init();

$(function() {
    'use strict';
    switch(_pages){
        case 'home':
            Home.init();
        break;
        case 'about':
            About.init();
        break;
        case 'get-in-touch':
            GetInTouch.init();
        break;
    }

    $( window ).on('resize', function() {
        switch(_pages){
            case 'home':
                Home.resize();
            break;
            case 'about':
                About.resize();
            break;
            case 'get-in-touch':
                GetInTouch.resize();
        break;
        }
    });

});
