// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import 'slick-carousel';



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $teams		= $('.teams:not(.not-slick)');
var $slides     = $teams.find('.items');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($teams.length){

    $slides.slick({
		arrows				: false,
		dots				: false,
        variableWidth		: true,
	});

	$teams.find('.a-left').on('click',function(){
		$slides.slick('slickPrev');
	});

	$teams.find('.a-right').on('click',function(){
		$slides.slick('slickNext');
	});
}



