// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import * as Flickity from "flickity";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $homeClients  = $('.home-clients');

var _fcktBack     = null;
var _fcktFor      = null;
var _lineBack     = document.querySelector('.line-backward');
var _lineFor      = document.querySelector('.line-forward');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
function init(){

  _fcktBack = new Flickity(_lineBack, {
    autoPlay            : false,
    prevNextButtons     : false,
    pageDots            : false,
    draggable           : false,
    wrapAround          : true,
    selectedAttraction  : 0.015,
    friction            : 0.25
  });

  _fcktBack.x = 0;

  _fcktFor = new Flickity(_lineFor, {
    autoPlay            : false,
    prevNextButtons     : false,
    pageDots            : false,
    draggable           : false,
    wrapAround          : true,
    selectedAttraction  : 0.015,
    friction            : 0.25
  });

  _fcktFor.x = 0;

  update();

}



// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ------------ \\\
// ----------------------------------------- \\\



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function update(){
  if (_fcktBack.slides) {
      _fcktBack.x = (_fcktBack.x - .5) % _fcktBack.slideableWidth;
      _fcktBack.selectedIndex = _fcktBack.dragEndRestingSelect();
      _fcktBack.updateSelectedSlide();
      _fcktBack.settle(_fcktBack.x);
  }

  if (_fcktFor.slides) {
      _fcktFor.x = (_fcktFor.x + .5) % _fcktFor.slideableWidth;
      _fcktFor.selectedIndex = _fcktFor.dragEndRestingSelect();
      _fcktBack.updateSelectedSlide();
      _fcktFor.settle(_fcktFor.x);
  }
  window.requestAnimationFrame(update);
};


// ----------------------------------------- \\\
// ---------------- EXPORTS ---------------- \\\
// ----------------------------------------- \\\
export { init }