import $ from "jquery";



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $filter         = $('.filter');
var $btSearch       = $filter.find('.i-search');
var $modalSearch    = $('.search-modal');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($filter.length){
	$btSearch.on('click',function(){
        $modalSearch.fadeIn(200).css('display','flex');
        $(this).addClass('active');
        $('.header').addClass('open-search');
        $('.pages').addClass('open-search');
    });
}



