// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import 'slick-carousel';



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $rGallery = $('.resources-gallery');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($rGallery.length){
	$rGallery.each(function(){
		addSlick($(this), $(this).find('.items'));
	});
}


// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\

function addSlick(_element, _slides){

	var _lenght = 100 / $(_slides).find('.item').length;	

	$(_slides).slick({
		arrows			: false,
		dots			: false,
		padding			: '60px',
		variableWidth	: true,
		infinite		: true,
		slidesToShow	: 1,
		responsive: [
			{
			  breakpoint: 890,
			  settings: {
				slidesToShow: 1,
				slidesToScroll: 1,
			  }
			}
		  ]
	});

	$(_slides).on( 'afterChange', function( e, slick, current ){
		var _width = _lenght * (current + 1);
		$(_element).find('.rg-bar').css({'width': _width+'%'});
	});

	$(_slides).on('beforeChange', function(event, slick, currentSlide, nextSlide){
		
	});

	$(_element).find('.a-left').on('click',function(){
		$(_slides).slick('slickPrev');
	});

	$(_element).find('.a-right').on('click',function(){
		$(_slides).slick('slickNext');
	});

}


