// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";


// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $menuNav        = $('.menu-navigate');
var $items          = $menuNav.find('.item');
var $footer         = $('.footer');
var $elements       = $('div');

var _scrollTop      = null;
var _offTop         = null;
var _offBtm         = null;
var _sizeScroll     = null;
var _elemPos        = [];


// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
if($menuNav.length){

    setTimeout(function(){
        $elements.each(function(i,e){
            if($(e).attr('data-id')){
                _elemPos.push($(e).offset().top-100);
            }
        });
    
        _offTop         = $menuNav.offset().top;
        _offBtm         = ($footer.offset().top + 40) - $menuNav.outerHeight();
        _sizeScroll     = $(document).height() - $(window).height() - $footer.outerHeight() - $menuNav.offset().top;
    
        $( window ).on('resize', function() {
            _offTop         = $menuNav.offset().top;
            _offBtm         = ($footer.offset().top + 40) - $menuNav.outerHeight();
            _sizeScroll     = $(document).height() - $(window).height() - $footer.outerHeight() - $menuNav.offset().top;
        });
    
        $(window).on('scroll',function(e){
    
            _scrollTop = $(window).scrollTop();
    
            var _barPercent = ((_scrollTop - _offTop) * 100) / _sizeScroll;
    
            if (_scrollTop > _offTop && $menuNav.hasClass('natural')) {
                $menuNav.removeClass('natural').addClass('fixed');
            }
    
            if (_offTop > _scrollTop && $menuNav.hasClass('fixed')) {
                $menuNav.removeClass('fixed').addClass('natural');
            }
    
            if (_scrollTop > _offBtm && $menuNav.hasClass('fixed')) {
              $menuNav.removeClass('fixed').addClass('bottom').css('top', _offBtm - (_offTop));
            }
            if (_offBtm > _scrollTop && $menuNav.hasClass('bottom')) {
                $menuNav.removeClass('bottom').addClass('fixed').css('top', '0px');
            }
    
            for(var i = 0; i < _elemPos.length; i++){
                var _top        = _elemPos[i];
                var _nextTop    = (_elemPos[i+1]) ? _elemPos[i+1] : $footer.offset().top;
    
                if(_scrollTop > _top && _scrollTop <= _nextTop){
                    setSubmenu(i)
                }
            }
    
    
            $menuNav.find('.l-bar').css('height', _barPercent+'%');
    
        });
            
        $items.on('click',function(e){
            e.preventDefault();
            var _target = $(this).attr('data-target');
            var offset  = $('[data-id="'+_target+'"]').offset().top - $('.header').outerHeight();
            $('html,body').animate({ scrollTop: offset, }, 1000); 
        });
    },300);
}


// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ------------ \\\
// ----------------------------------------- \\\



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function setSubmenu(_pos){
    $menuNav.find('.item').removeClass('active');
    $menuNav.find('.item').eq(_pos).addClass('active');

}