// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import * as Map from "../components/map"



// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $map          = $('.map');
var $continents   = $map.find('.item');
var $place        = $map.find('.p-item');
var $offices      = $('.offices');



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
function init(){
   if($(window).width() < 850){
      $offices.find('.item').on('click',function(){
         if($(this).hasClass('active')){
            $(this).removeClass('active');
            $(this).find('.contacts').slideUp();
         }else{
            $(this).addClass('active');
            $(this).find('.contacts').slideDown();
         }
      });

      $offices.find('.item').eq(0).off('click');
   }

   $continents.find('.label').on('click',function(){
      $continents.find('.label').removeClass('active');
      $continents.find('.place').fadeOut(100);

      $(this).addClass('active');
      $(this).parent().find('.place').fadeIn(300).css('display','flex');
   })

   $place.on('click',function(){
      $place.removeClass('active');
      $(this).addClass('active');

      var _lat = $(this).attr('data-lat');
      var _lon = $(this).attr('data-long');
      var _add = $(this).attr('data-address');

      setMap(_lat,_lon,_add);
   });


   var _latInit      = $place.eq(0).attr('data-lat');
   var _lonInit      = $place.eq(0).attr('data-long');
   var _addressInit  = $place.eq(0).attr('data-address');

   $place.eq(0).addClass('active');
   Map.init(_latInit, _lonInit, _addressInit);

}



// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ------------ \\\
// ----------------------------------------- \\\
function resize() { 

}



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\

function setMap(_lat, _long, _address) { 
   Map.buildMap(_lat, _long, _address);
}


// ----------------------------------------- \\\
// ---------------- EXPORTS ---------------- \\\
// ----------------------------------------- \\\
export { init, resize }




