// ----------------------------------------- \\\
// ---------------- IMPORTS ---------------- \\\
// ----------------------------------------- \\\
import $ from "jquery";
import * as HClients from "../components/home-clients"
import * as ScrollMagic from  'scrollmagic';
import gsap, {TimelineMax, TweenMax, Back} from "gsap";




// ----------------------------------------- \\\
// ----------------- VARS ------------------ \\\
// ----------------------------------------- \\\
var $pages       	= $('.pages');
var $header         = $('.header');
var $scrollDown     = $('.scrolldown');
var $homeBullets    = $('.home-bullets');

var _controller     = null;
var _wHeight        = window.innerHeight;
var _scrollPos      = 0;
var _scrollValues   = [];



// ----------------------------------------- \\\
// ------------------ INIT ----------------- \\\
// ----------------------------------------- \\\
function init(){

    if($(window).width() > 850){
        $scrollDown.on('click', function(){
            $("html, body").animate({ scrollTop: $('.pc-home-video').offset().top-120 }, "slow");	
        });

        addScrollMagic();
        resize();
    }

    HClients.init();

}



// ----------------------------------------- \\\
// ------------ PUBLIC FUNCIONS ------------ \\\
// ----------------------------------------- \\\
function resize() { 
    if($(window).width() > 850){
        _controller.update(true);
    }
}



// ----------------------------------------- \\\
// ------------ PRIVATE FUNCIONS ----------- \\\
// ----------------------------------------- \\\
function addScrollMagic(){

    _controller = new ScrollMagic.Controller({
        globalSceneOptions: {
            triggerHook: 0,
            reverse: true
        }
    });

    // HOME WEBDOOR
    var homeWebdoor = new TimelineMax ()
    .add([
        TweenMax.fromTo($('.pc-home-webdoor').find('.title'),   1, {y: -100}, {y: -400}),
        TweenMax.fromTo($('.pc-home-webdoor').find('.button:not(.b-scroll)'),  1, {y: -60}, {y: -400}),
        TweenMax.fromTo($('.pc-home-webdoor').find('.image'),   1, {y: 0}, {y: -180}),
        TweenMax.fromTo($('.pc-home-webdoor').find('.vector'),  1, {y: 0}, {y: 90}),
    ]);

    new ScrollMagic.Scene({triggerElement: $('body')[0], duration: "100%"})
                    .setTween(homeWebdoor)
                    .addTo(_controller);   


    // HOME VIDEO
    var homeVideo = new TimelineMax ()
    .add([
        TweenMax.fromTo($('.video'),   1, {y: -_wHeight/3, scale: 1}, {y: 0, scale: 1.3}),
    ]);
    
    new ScrollMagic.Scene({triggerElement: $('.home-numbers')[0], duration: "100%"})
                    .setTween(homeVideo)
                    .addTo(_controller); 
                    
     // HOME NUMBERS
     var homeNumbers = new TimelineMax ()
     .add([
        TweenMax.fromTo($('.hm-image-1'), 2, {y: -50}, {y: -250}),
        TweenMax.fromTo($('.hm-number-1'), 4, {y: 400}, {y: -250}),
        TweenMax.fromTo($('.hm-number-2'), 2, {y: 400}, {y: -120}),
        TweenMax.fromTo($('.hm-image-2'), 6, {y: 400}, {y: -100}),
        TweenMax.fromTo($('.hm-image-3'), 5, {y: 420}, {y: -280}),
        TweenMax.fromTo($('.hm-number-3'), 5, {y: 120}, {y: 0}),
     ]);
     
     new ScrollMagic.Scene({triggerElement: $('body')[0], duration: "150%"})
                     .setTween(homeNumbers)
                     .addTo(_controller); 


    // HOME DESC
    var homeDesc = new TimelineMax ()
    .add([
        TweenMax.fromTo($('.home-desc').find('.left').find('.paragraph'),   2, {y: 100}, {y: -20}),
        TweenMax.fromTo($('.home-desc').find('.button'),  2, {y: 100}, {y: -20}),
        TweenMax.fromTo($('.home-desc').find('.right'),   2, {y: 120}, {y: -100}),
    ]);

    new ScrollMagic.Scene({triggerElement: $('.home-desc')[0], duration: "150%", offset:-_wHeight/2})
                    .setTween(homeDesc)
                    .addTo(_controller);
                    

    // HOME Phrase
    var homePhrase = new TimelineMax ()
    .add([
        TweenMax.fromTo($('.pc-home-phrase').find('.title'),   1, {y: 200}, {y: -100}),
        TweenMax.fromTo($('.pc-home-phrase').find('.button'),   1, {y: 180}, {y: -50}),
    ]);

    new ScrollMagic.Scene({triggerElement: $('.pc-home-phrase')[0], duration:'100%', offset:-_wHeight/2})
                    .setTween(homePhrase)
                    // .addIndicators()
                    .addTo(_controller);


    // HOME HIGHLIGHTS
    var homeHighlights = new TimelineMax ()
    .add([
        // TweenMax.fromTo($('.home-highlights').find('.home-navigate'),   1, {y: 400}, {y: 0}),
    ]);

    var sceneHomeSolution = new ScrollMagic.Scene({ triggerElement: $('.pc-home-solutions')[0], duration: '400%' })
                    .setPin('.pc-home-solutions')
                    // .addIndicators()
                    .on("progress", function (e) {
                        var _perc = Math.round((e.progress * 100)/25);
                        
                        $('.pc-home-solutions').find('.home-navigate').find('.hn-item').removeClass('active');
                        $('.pc-home-solutions').find('.home-navigate').find('.hn-item').eq(_perc).addClass('active');

                        $('.pc-home-solutions').find('.item').removeClass('motion-in');
                        $('.pc-home-solutions').find('.item').eq(_perc).addClass('motion-in');

                        console.log(_perc)
                    })
                    .addTo(_controller);
    
    new ScrollMagic.Scene({triggerElement: $('.pc-home-solutions')[0], duration: "150%"})
                    .setTween(homeHighlights)
                    .addTo(_controller);    


    _controller.scrollTo(function (newpos) {
        console.log(newpos);
        TweenMax.to(window, 1, {scrollTo: {y: newpos, ease: 'Power3.easeOut'}});
    });

    $('.pc-home-solutions').find('.home-navigate').find('.hn-item').on('click',function(){

        var _target = $(this).data('target');
        var _sceneD = sceneHomeSolution.duration()/5;

        if(_target == 'assessment'){
            _controller.scrollTo(sceneHomeSolution.scrollOffset());
        }else if(_target == 'development'){
            _controller.scrollTo(sceneHomeSolution.scrollOffset()+ (_sceneD*1));
        }else if (_target == 'teams'){
            _controller.scrollTo(sceneHomeSolution.scrollOffset()+ (_sceneD*3));
        }else if (_target == 'founder'){
            _controller.scrollTo(sceneHomeSolution.scrollOffset() + (_sceneD*4));
        }else if (_target == 'diversity'){
            _controller.scrollTo(sceneHomeSolution.scrollOffset() + (_sceneD*5));
        }
        
    });

                    
    // HOME Phrase
    var homeGrayBlack = new TimelineMax ()
    .add([
        // TweenMax.fromTo($('.pc-home-phrase').find('.title'),   1, {y: 200}, {y: -100}),
        // TweenMax.fromTo($('.pc-home-phrase').find('.button'),   1, {y: 180}, {y: -50}),
    ]);

    new ScrollMagic.Scene({triggerElement: $('.pc-home-clients')[0], duration:'100%', offset:-_wHeight/2})
                    .setTween(homeGrayBlack)
                    .addTo(_controller);

    // HOME gray
    var homeGray = new TimelineMax ()
    .add([
        // TweenMax.fromTo($('.pc-home-phrase').find('.title'),   1, {y: 200}, {y: -100}),
        // TweenMax.fromTo($('.pc-home-phrase').find('.button'),   1, {y: 180}, {y: -50}),
    ]);

    new ScrollMagic.Scene({triggerElement: $('.pc-home-spotlight')[0], duration:'100%', offset:-_wHeight/2})
                    .setTween(homeGray)
                    .addTo(_controller);


    // HOME RESOURCES
    var homeResources = new TimelineMax ()
    .add([
        TweenMax.staggerFromTo($('.pc-home-resources').find('.item'), 2, {y: 100}, {y: -100, ease: Back.easeOut}, 0.15),
        TweenMax.fromTo($('.pc-home-resources').find('.title'),   1, {y: 0}, {y: -100}),
        TweenMax.fromTo($('.pc-home-resources').find('.button'),   1, {y: 0}, {y: -100}),
    ]);

    new ScrollMagic.Scene({triggerElement: $('.pc-home-resources')[0], duration:'200%', offset:-_wHeight/2})
                    .setTween(homeResources)
                    .addTo(_controller);

}


// ----------------------------------------- \\\
// ---------------- EXPORTS ---------------- \\\
// ----------------------------------------- \\\
export { init, resize }




